import { Component, OnInit, ViewChild } from '@angular/core';
import { FilestructureService } from './filestructure.service';
import {Router} from '@angular/router';
import {saveAs} from 'file-saver';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import {MatSort} from '@angular/material/sort';

export interface PeriodicElement {
  file_name: string;
  size: string;
}

@Component({
  selector: 'app-filestructure',
  templateUrl: './filestructure.component.html',
  styleUrls: ['./filestructure.component.css']
})
export class FilestructureComponent implements OnInit {

  folder_list:any;
  folderlist:PeriodicElement[]=[];
  displayedColumns: string[] = ['file_name','size','create_time','last_write_time','action'];
  dataSource: MatTableDataSource<any>;

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  constructor(private FilestructureService:FilestructureService,private router:Router) {
    this.FilestructureService.getFileList().subscribe((data: any[])=>{
      console.log(data);
      this.folder_list=data;
      this.dataSource = new MatTableDataSource(data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
   }

   applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  ngOnInit(): void {
  }

  doLogout(){
    sessionStorage.setItem('isLoggedIn','false');    
    sessionStorage.removeItem('token'); 
    this.router.navigate(['/']);
}

  btnDownload(row) {
    let filename = row['file_name'];
    console.log(filename);
    this.FilestructureService.downloadFile(filename).subscribe(response => {
      let blob:any = new Blob([response]);
			const url = window.URL.createObjectURL(blob);
			saveAs(blob, filename);
		}), error => console.log('Error downloading the file'),() => console.info('File downloaded successfully');
  }

}
