import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {LoginService} from './login.service';

@Component({
    selector:'login',
    templateUrl:'./login.component.html',
     styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit{
    buttonDisable:boolean=false;
    serverdate:Date;
    displayLoginFailureBlock:boolean = false;
    password:string;
    username:string;

    constructor(private loginService: LoginService,private _router: Router) { }

    ngOnInit(): void {
        sessionStorage.setItem('isLoggedIn','false');
        this.serverdate = new Date();
        var dd= this.serverdate.getDate();
        var mm= this.serverdate.getMonth()+1;
        var year = this.serverdate.getFullYear();
        // console.log(this.serverdate,dd,mm,year);
        if(mm >= 12 && dd >= 21)
        {
          this.buttonDisable = true;
        }
        this.buttonDisable = false;
    }
  

    doLogin(event){
        this.loginService.doLogin(this.password,this.username).subscribe((data: any[])=>{
            console.log(data);
            if (data['token'] !== undefined){
                sessionStorage.setItem("token",data['token']);
                sessionStorage.setItem('isLoggedIn', "true");
                this._router.navigate(['clusters']);
                this._router.navigate(['filelist']);
            }
            else{
                this.displayLoginFailureBlock = true;
            }
        })
    }
    
}