import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpParams} from '@angular/common/http';
import {  throwError, Observable } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { environment } from '../../environments/environment';


@Injectable({
  providedIn: 'root'
})
export class FilestructureService {
  
  private baseUrl=environment.baseURL;
  constructor(private httpClient: HttpClient) { }

  handleError(error: HttpErrorResponse) {
    let errorMessage = 'Unknown error!';
    if (error.error instanceof ErrorEvent) {
      // Client-side errors
      errorMessage = `Error: ${error.error.message}`;
    } else {
      // Server-side errors
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    // window.alert(errorMessage);
    return throwError(errorMessage);
  }
  public getFileList(){
    let API_URL_TMP = this.baseUrl+"filelist"; 
    console.log(sessionStorage.getItem('token'));
    let header = {'x-access-tokens': sessionStorage.getItem('token')};
    return this.httpClient.get(`${API_URL_TMP}`,{headers:header}).pipe(retry(1), catchError(this.handleError));
  }

  downloadFile(filename): Observable< ArrayBuffer > {
    const API_URL = this.baseUrl+"downloadfile";
    let header = {'x-access-tokens': sessionStorage.getItem('token')};
    let httpParams = new HttpParams().set("filename",filename);
		return this.httpClient.get(API_URL,{responseType: 'arraybuffer', headers: header, params:httpParams});
  }
}
