import { Injectable } from '@angular/core';
import {FormGroup } from '@angular/forms';
import { HttpClient, HttpErrorResponse, HttpParams, HttpHeaders} from '@angular/common/http';
import {  throwError, Observable } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class FilemanagerService {

  private baseUrl=environment.baseURL;
  constructor(private httpClient: HttpClient) { }
  handleError(error: HttpErrorResponse) {
    let errorMessage = 'Unknown error!';
    if (error.error instanceof ErrorEvent) {
      // Client-side errors
      errorMessage = `Error: ${error.error.message}`;
    } else {
      // Server-side errors
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    // window.alert(errorMessage);
    return throwError(errorMessage);
  }
  public getFileList(folder){
    let API_URL_TMP = this.baseUrl+"filelist"; 
    // console.log(sessionStorage.getItem('token'));
    let header = {'x-access-tokens': sessionStorage.getItem('token')};
    let httpParams = new HttpParams().set("folder_name",folder);
    return this.httpClient.get(`${API_URL_TMP}`,{headers:header,params:httpParams}).pipe(retry(1), catchError(this.handleError));
  }

  public getFileListInsideFolder(service_name,path){
    let API_URL_TMP = this.baseUrl+"folderfilelist"; 
    // console.log(sessionStorage.getItem('token'));
    let header = new HttpHeaders({'x-access-tokens': sessionStorage.getItem('token')});
    console.log(service_name,path);
    let httpParams= new HttpParams();
    httpParams=httpParams.append('service_name',service_name);
    httpParams=httpParams.append('path',path);
    // const form = new FormData();
    // form.append('service_name',path);
    // form.append('path',path);
    return this.httpClient.get(`${API_URL_TMP}`,{headers:header,params:httpParams}).pipe(retry(1), catchError(this.handleError));
  }

  public getFolderList(){
    let API_URL_TMP = this.baseUrl+"folderlist"; 
    // console.log(sessionStorage.getItem('token'));
    let header = {'x-access-tokens': sessionStorage.getItem('token')};
    return this.httpClient.get(`${API_URL_TMP}`,{headers:header}).pipe(retry(1), catchError(this.handleError));
  }

  downloadFile(path,service_name): Observable< ArrayBuffer > {
    const API_URL = this.baseUrl+"downloadfile";
    let header = {'x-access-tokens': sessionStorage.getItem('token')};
    let httpParams = new HttpParams();
    httpParams=httpParams.append('service_name',service_name);
    httpParams=httpParams.append('path',path);
		return this.httpClient.get(API_URL,{responseType: 'arraybuffer', headers: header, params:httpParams});
  }

  uploadFile(fileToUpload: File,service_name,path){
    const endPoint = this.baseUrl+"uploadFile";
    let header = new HttpHeaders({'x-access-tokens': sessionStorage.getItem('token')});
    const formData: FormData = new FormData();
    formData.append('file', fileToUpload);
    formData.append('service_name',service_name);
    formData.append('path',path);
    return this.httpClient.post(endPoint,formData, {headers:header}).pipe(retry(1),catchError(this.handleError));
  }
}
